* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

#Header {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: #0b0e33;
  height: 100vh;
}
#Header #container-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
#Header #container-logo #logo-game {
  width: 50vh;
  max-width: 700px;
  margin: 0 auto;
}
#Header #menu-principal {
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 7vh;
  background-color: black;
  top: 0;
  left: 0;
}
#Header #menu-principal #open-menu {
  display: none;
}
#Header #menu-principal #Menu {
  display: flex;
  align-items: center;
  margin: 0 auto;
  list-style: none;
}
#Header #menu-principal #Menu a {
  color: silver;
  font-size: 20px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  display: flex;
  width: 150px;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  height: inherit;
}
#Header #menu-principal #Menu a:hover {
  color: white;
  transition-delay: 0.5s;
  transition-delay: initial;
}
#Header #menu-principal #Menu #close-menu {
  display: none;
}
#Header #menu-secundario {
  display: none;
}

.App .algo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vw;
  flex-direction: column;
  justify-content: space-evenly;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.App .algo .contenido {
  display: flex;
  padding: 80px;
  border-radius: 40px;
  width: 90vw;
  flex-direction: row;
  align-items: center;
  background-color: #f5f5f5;
  justify-content: space-evenly;
}
.App .algo .contenido .inf {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 50vw;
  height: 29vw;
}
.App .algo .contenido .inf h1 {
  padding-bottom: 20px;
  font-size: 2vw;
}
.App .algo .contenido .inf p {
  font-size: 1.3vw;
}
.App .algo .imagen img {
  width: 18vw;
}
.App .slider {
  display: flex;
  background: #514A9D;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.App .slider .inf {
  padding-top: 80px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 20px;
  color: white;
}
.App #App-body {
  background-color: whitesmoke;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  font-family: Arial, Helvetica, bold;
}
.App #App-body .title {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background: #4a6d9d;
  width: 50vw;
}
.App #App-body .title img {
  width: 15vw;
}
.App #App-body .contenedor-img {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50vw;
}
.App #App-body .contenedor-img img {
  box-shadow: 22px 11px 23px 13px rgba(0, 0, 0, 0.356);
  box-shadow: 22px 11px 23px 13px rgba(0, 0, 0, 0.33);
  border-radius: 2vw;
  width: 30vw;
}
.App .gdd {
  background: #514A9D;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40vw;
  flex-direction: row;
  justify-content: space-evenly;
}
.App .gdd .container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20vw;
  width: 42vw;
  border-radius: 2vw;
  background-color: white;
  -webkit-box-shadow: 5px 2px 15px 4px rgba(0, 0, 0, 0.48);
  box-shadow: 5px 2px 15px 4px rgba(0, 0, 0, 0.48);
}
.App .gdd .container img {
  width: 40vw;
  border-radius: 2vw;
}

