#login .Login {
  width: 100%;
  min-height: 100vh;
  padding: 0 20px;
  background: #e9e9e9;
  display: flex;
}
#login .Login .loginContainer {
  padding: 60px;
  margin: auto;
  width: 100%;
  max-width: 520px;
  min-height: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: radial-gradient(ellipse at left bottom, #16182f 0%, rgba(38, 20, 72, 0.9) 59%, rgba(17, 27, 75, 0.9) 100%);
  box-shadow: 0 50px 70px -20px rgba(0, 0, 0, 0.8);
}
#login .Login .loginContainer label {
  color: white;
  margin: 14px 0;
  display: block;
  font-size: 22px;
  line-height: 1;
}
#login .Login .loginContainer input {
  width: 100%;
  border: none;
  outline: none;
  font-size: 19px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  letter-spacing: 1px;
}
#login .Login .loginContainer .btnContainer {
  width: 100%;
  padding: 24px 0;
}
#login .Login .loginContainer .btnContainer button {
  border: none;
  outline: none;
  width: 100%;
  padding: 15px 0;
  color: #fff;
  font-size: 16px;
  letter-spacing: 1px;
  background: #603bbb;
  cursor: pointer;
}
#login .Login .loginContainer .btnContainer p {
  margin: 14px 0 0 0;
  text-align: right;
  color: #fff;
}
#login .Login .loginContainer .btnContainer p span {
  color: yellow;
  font-weight: 500;
  letter-spacing: 0.5px;
  margin-left: 5px;
  cursor: pointer;
  transition: all 400ms ease-in-out;
}
#login .Login .loginContainer .btnContainer p span:hover {
  color: red;
}
#login .Login .loginContainer .errorMsg {
  color: red;
  font-size: 16px;
}
#login .hero {
  width: 100%;
  min-height: 100vh;
  background: radial-gradient(ellipse at left bottom, #16182f 0%, rgba(38, 20, 72, 0.9) 59%, rgba(17, 27, 75, 0.9) 100%);
}
#login .hero nav {
  background: #603bbb;
  width: 100%;
  padding: 20px 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
#login .hero nav h2 {
  color: #fff;
}
#login .hero nav button {
  width: 86px;
  padding: 8px 0;
  background: #3a2b58;
}

